<template>
  <v-container ref="boxIds" :class="isApp ? 'activity_detail_container_app' : 'activity_detail_container_pc'" app>
    <div class="scroll_box" v-show="show">
      <div v-if="detail && detail.img">
        <img class="activity_detail_img" v-if="load_status" :src="detail.img" />
        <v-skeleton-loader v-else class="activity_detail_img" type="image"></v-skeleton-loader>
      </div>
      <div class="detail_text" v-html="detail && getDetailContent(detail.content)"></div>
      <img v-if="detail.default_img" :src="detail.default_img" class="default_img" />
      <div v-if="defaultContent" class="detail_text" v-html="defaultContent"></div>
      <div v-if="detail && detail.button_name" class="btn_box">
        <v-btn color="t_btn_host_color" @click="btnClick(detail)" class="detail_btn btn_radius">
          {{ detail.button_name }}
        </v-btn>
      </div>
    </div>
    <div v-show="!show" class="scroll_box">
      <space-ui sign="activity_detail" />
    </div>
  </v-container>
</template>

<script>
import { mapState } from 'vuex'
import ContainerMixin from '@/components/containerMixin'
import { jump } from '@/utils/linkMap'
import spaceUi from '@/components/skeleton/space-ui.vue'
import { getEeplace, lodash } from '@/utils/helper.js'

export default {
  name: 'activityDetail',
  mixins: [ContainerMixin],
  components: {
    spaceUi
  },
  data() {
    return {
      ui: {},
      detail: {},
      list: [],
      show: false,
      time: 5,
      load_status: false,
      actId: ''
    }
  },
  watch: {
    // pc 端左侧栏点击的时候监听
    $route(route) {
      this.getList()
    }
  },
  computed: {
    ...mapState('base', ['user', 'languageType', 'isApp']),
    ...mapState('source', ['rechargeInfo', 'activity']),
    // 默认活动文本
    defaultContent() {
      const { default_content = '' } = this.detail
      return getEeplace(default_content)
    }
  },
  created() {
    this.getList()
  },
  methods: {
    getDetailContent(content) {
      return getEeplace(content)
    },
    // 加载图片
    async loadImg() {
      const res = await this.$u.isImgLoad(this.detail.img).catch(e => e)
      if (res) {
        this.load_status = true
      } else {
        if (this.time > 1) {
          this.time--
          this.loadImg()
        }
      }
    },
    getList() {
      this.show = false
      if (typeof this.$route.query.act_id == 'string') {
        const act_id = this.$route.query.act_id
        this.actId = act_id
        if (this.activity && this.activity[act_id]) {
          this.detail = this.activity[act_id]
          this.detail.button_name = this.activity[act_id].btn_txt || ''
        }
      } else {
        let data = this.rechargeInfo
        if (data) {
          delete data.activity_config
          this.list = Object.keys(data)
            .map(item => ({
              ...data[item],
              id: data[item].act_id,
              img_src: ''
            }))
            .filter(v => !!v.act_id)
          this.detail = this.list.find(v => v.act_id == this.$route.query.act_id)
        } else {
          this.detail = {}
        }
      }
      if (this.detail.img) this.loadImg()
      setTimeout(() => {
        this.show = true
      }, 200)
    },
    /*
     *如果有配置外链点击需要跳转过去，需要判断是否有没有'http://'
     *
     */
    btnClick(item) {
      jump(item, this)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/activity.modules.scss';

.activity_detail_container_app {
  padding: $activity_detail_container_app_padding;
  .scroll_box {
    .default_img {
      width: $activity_detail_default_img_app_width;
      margin: $activity_detail_default_img_app_margin;
    }
  }
}

.activity_detail_container_pc {
  padding: $activity_detail_container_pc_padding;
}

.scroll_box {
  margin: $activity_detail_box_margin;
  font-family: $font-family-base;
  width: $activity_detail_box_width;

  .activity_detail_img {
    width: $activity_detail_box_img_width;
    border-radius: $activity_detail_box_img_radius;
    aspect-ratio: 2/1;
  }

  .detail_text {
    font-size: $activity_detail_box_text_size;
    font-weight: $activity_detail_box_text_bold;
    margin: $activity_detail_box_text_margin;
    white-space: pre-wrap;
    word-break: break-all;
    ::v-deep table {
      width: $activity_detail_table_width;
      border: 1px solid $activity_detail_table_border_color;
      tr {
        border-bottom: 1px solid $activity_detail_table_tr_border_color;
        td {
          border-right: 1px solid $activity_detail_table_td_border_color;
        }
      }
    }
    ::v-deep img {
      width: $activity_detail_table_img_width;
    }

    ::v-deep em {
      font-style: italic !important;
    }

    ::v-deep strong {
      font-weight: 900 !important;
    }

    ::v-deep hr {
      overflow: hidden !important;
      border-style: inset !important;
      border-width: 1px !important;
      margin: 20px auto !important;
    }
  }
  .default_img {
    display: flex;
    max-width: $activity_detail_default_img_max_width;
    margin: $activity_detail_default_img_pc_margin;
  }

  .btn_box {
    @include setBox(row, center, center);
    margin: $activity_detail_box_btn_margin;

    .detail_btn {
      min-width: $activity_detail_box_btn_min_width;
      height: $activity_detail_box_btn_height;
      font-size: $activity_detail_box_btn_size;
      font-weight: $activity_detail_box_btn_bold;
    }
  }
}
::v-deep p {
  margin-bottom: 0px;
}
</style>
